.paginator {
  display: flex;
  align-items: center;
}

.pageList {
  display: flex;
  flex-wrap: wrap;
}

.pageItem:not(:first-child) {
  margin-left: 5px;
}

.pageButton {
  padding: 8px 7px;
}

.pageButton.selectedPage {
  font-weight: bold;
  background: #379683;
}

.button {
  font-size: 14px;
  background: #05386B;
  color: #EDF5E1;
}

.buttonPrev {
  margin-right: 7px;
}

.buttonNext {
  margin-left: 7px;
}